export default function Statistique() {
  const { themeStretch } = useSettings();
  const theme = useTheme();
  const { user } = useAuth();
  const dispatch = useDispatch();
  const { statistiques, agents, surcussales, lotto, infotirages } = useSelector((state) => state.user);
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('boule');
  const [value, setValue] = useState(new Date());
  const [value1, setValue1] = useState(new Date());
  const [value2, setValue2] = useState(user?.role !== 'superuser' ? 1 : 'BO');
  const [value21, setValue21] = useState(0);
  const [value3, setValue3] = useState(0);
  const [value4, setValue4] = useState(0);
  const [loading, setLoading] = useState(false);
  const onChangeDate1 = (datavalue) => {
    setValue(datavalue);
  };
  const onChangeDate2 = (datavalue) => {
    setValue1(datavalue);
  };
  const handleGo = async () => {
    setLoading(true);
    await dispatch(getStatistiques(fDate(value), fDate(value1), value21, value2, value3, value4));
    setLoading(false);
  };
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(25);

  useEffect(() => {
    // handleGo();
    dispatch(getAgents());
    dispatch(getSurcussales());
    dispatch(getLottos());
    dispatch(getInfoTirages());
  }, [dispatch]);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = statistiques?.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, id);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const handleDeleteStatistique = (bouleId) => {
    // dispatch(deleteStatistique(bouleId));
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - statistiques.length) : 0;

  const filteredStatistiques = applySortFilter(statistiques, getComparator(order, orderBy), filterName);

  const isStatistiqueNotFound = filteredStatistiques?.length === 0;

  return (
    <Page title="Statistique | RLP Center Lotto">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Statistiques"
          links={[{ name: 'Dashboard', href: PATH_DASHBOARD.root }, { name: 'Statistiques' }]}
        />

        <Card>
          <StatistiqueListToolbar
            value={value}
            value1={value1}
            value2={value2}
            value21={value21}
            value3={value3}
            value4={value4}
            setValue2={setValue2}
            setValue21={setValue21}
            setValue3={setValue3}
            setValue4={setValue4}
            onChangeDate1={onChangeDate1}
            onChangeDate2={onChangeDate2}
            handleGo={handleGo}
            isStatistiqueNotFound={isStatistiqueNotFound}
            loading={loading}
            setLoading={setLoading}
            agents={agents}
            surcussales={surcussales}
            lottos={user?.role !== 'superuser' ? lotto : OPTIONS}
            tirages={user?.role !== 'superuser' ? infotirages : TIRAGE}
          />

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table>
                <StatistiqueListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={statistiques?.length}
                  numSelected={selected?.length}
                  onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                />
                <TableBody>
                  {(rowsPerPage > 0
                    ? filteredStatistiques?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    : filteredStatistiques
                  )?.map((ite) => {
                    const isItemSelected = selected.indexOf(ite?.id) !== -1;

                    return (
                      <TableRow
                        hover
                        key={ite?.id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell align="left">{ite?.type}</TableCell>
                        <TableCell align="left">{ite?.boule}</TableCell>
                        <TableCell align="left">{ite?.quantite}</TableCell>
                        <TableCell align="left">{parseFloat(ite?.montant).toFixed(2)}</TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(ite?.block && 'error') || 'success'}
                          >
                            {ite?.block ? 'Oui' : 'Non'}
                          </Label>
                        </TableCell>
                        <TableCell align="right">
                          <BouleMoreMenu boule={ite?.boule} block={ite?.block} handleGo={handleGo} />
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height: 53 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
                {isStatistiqueNotFound && (
                  <TableBody>
                    <TableRow>
                      <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                        <SearchNotFound searchQuery={filterName} />
                      </TableCell>
                    </TableRow>
                  </TableBody>
                )}
              </Table>
            </TableContainer>
          </Scrollbar>

          <TablePagination
            labelRowsPerPage="Lignes par page"
            rowsPerPageOptions={[25, 50, 75, 100, { label: 'Tout', value: -1 }]}
            component="div"
            count={statistiques?.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </Card>
      </Container>
    </Page>
  );
}
